import React from 'react';
import {BrowserRouter as Router, Route, Switch, Link} from "react-router-dom";
import Auth from "./components/Authenticate/auth";
import MainPage from "./components/Dashboard/MainPage";

function App() {
    return (
        <Router>
            <Switch>
                <Route path={'/login'}>
                    <Auth type={'login'}/>
                </Route>
                <Route path={'/register'}>
                    <Auth type={'register'} />
                </Route>
                <Route path={'/dashboard'}>
                    <MainPage type={'dashboard'} />
                </Route>
                <Route path={'/question'}>
                    <MainPage type={'question'} />
                </Route>
                <Route path={'/question-detail'}>
                    <MainPage type={'detail'}/>
                </Route>
                <Route path={'/'}>
                    <Auth type={'login'} />
                </Route>
            </Switch>
        </Router>
    );
}

export default App;
